<template>
  <div id="app">
    <div class="cp-form">
      <div class="button-field">
        <button id='add-btn' class='add-btn' type="button" name="button" @click='addGroup'>增加</button>
      </div>
      <div class='group' v-for='(group, index) in list' v-bind:key='index'>
        <form-item :label="item.label" :name="item.name" :tip="item.tip" v-for="(item, n) in formItems" v-bind:key='n'>
          <CpInput v-if="item.type == 'input'" v-model="group[item.name]" :placeholder='item["placeholder"]'/>
          <CpSelect v-if="item.type == 'select'" :options="item.options" v-model="group[item.name]" />
        </form-item>
        <div style="color:red;font-size:12px;margin-bottom:10px;margin-top:10px;text-align:center;">
          按钮URL：javascript:void('{{group["packageName"]}}')
        </div>
        <button class='delete-btn' type="button" name="button" @click='deleteGroup(index)'>删除</button>
      </div>
    </div>
    <div class="moreField">
      <form-item :label="key" :name="key" v-for="(value, key) in moreInfoMap" v-bind:key='key'>
        <CpInput v-model="moreInfoMap[key]"/>
      </form-item>
      <div style="color:red;font-size:12px;margin-bottom:10px;margin-top:10px;text-align:center;">
        按钮URL：javascript:void('more')
      </div>
    </div>
    <div class='javascript-editor' id="javascript-editor"></div>
    <pre id='code' style='display:none;'>
(function(){
  var appInfoMap = {};
  var moreInfoMap = {};
  var keys = Object.keys(appInfoMap);
  parent.postMessage({event: 'initAppInfoMap', appInfoMap: appInfoMap},'*');
  parent.postMessage({event: 'initMoreInfoMap', moreInfoMap: moreInfoMap},'*');
  var addClickHandle = function(){
    keys.forEach(function(packageName){
      $('[data-url="javascript:void(\''+packageName+'\')"]').unbind().click(function(){
        parent.postMessage({event: 'doClick', packageName: packageName},'*');
        return false;
      });
    });
    var $moreBtn = $('[data-url="javascript:void(\'more\')"]');
    if($moreBtn.length){
      $moreBtn.unbind().click(function(){
        parent.postMessage({event: 'doMore'},'*');
        return false;
      });
    }
  };
  addClickHandle();
  window.addEventListener('orientationchange', function() {
    addClickHandle();
  });
  function isElementInViewport(el) {
    var rect = el.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom &lt;= (window.innerHeight || document.documentElement.clientHeight) &&
      rect.right &lt;= (window.innerWidth || document.documentElement.clientWidth)
    );
  }
  var timeout = null;
  function exposurePosition() {
    keys.forEach(function(packageName){
      var appInfo = appInfoMap[packageName];
      if(appInfo.hasExposure){
        return;
      }
      var el = document.querySelector('[data-url="javascript:void(\''+packageName+'\')"]');
      if(!el){
        clearTimeout(timeout);
        timeout = setTimeout(exposurePosition, 3000);
        return;
      }
      if (isElementInViewport(el)) {
        parent.postMessage({
          event: 'mzDoTrack',
          trackInfo: {
            action: 'exposure_position',
            apkname: appInfo.packageName,
            versionCode: appInfo.versionCode
          }
        },'*');
        appInfo.hasExposure = true;
      }
    });
  }
  if(location.search.indexOf('notOverFlow') == -1){
    $(document.body).css('overflow-x', 'hidden');
  }
  window.addEventListener('scroll', exposurePosition, true);
  exposurePosition();
})();
    </pre>
  </div>
</template>

<script>
import FormItem from '@/components/common/FormItem'
import CpInput from '@/components/common/CpInput'
import CpSelect from '@/components/common/CpSelect'
import * as ace from 'brace'
import 'brace/mode/javascript'
import 'brace/theme/monokai'

var code = ''
var editor = null

export default {
  name: 'app',
  data () {
    return {
      formItems: [
        { type: 'input', label: 'rpkMasterUrl', name: 'rpkMasterUrl' },
        { type: 'input', label: 'packageName', name: 'packageName' },
        { type: 'input', label: 'versionCode', name: 'versionCode' }
      ],
      list: [],
      moreInfoMap: {
        url: 'https://i3.mzres.com/resources/appStore/fast-app/special.html?special_id=2&source_raw_url_web=/resources/appStore/classification/views/cats45.html&source_page_name_web=cats&t=1526610103537',
        title: '快应用',
        pager_name: '快应用宣传页',
        source_page: '快应用宣传页',
        source_page_id: '快应用宣传页-10000'
      }
    }
  },
  computed: {
  },
  watch: {
    list: {
      handler: function (val) {
        editor && editor.setValue(this.getCode())
      },
      deep: true
    },
    moreInfoMap: {
      handler: function (val) {
        editor && editor.setValue(this.getCode())
      },
      deep: true
    }
  },
  methods: {
    getCode () {
      var map = {}
      this.list.forEach(function (item) {
        item.versionCode = (item.versionCode !== '' ? parseInt(item.versionCode) : '')
        map[item.packageName] = item
      })
      return code.replace(/var appInfoMap = \{\};/m, `var appInfoMap = ${JSON.stringify(map)};`).replace(/var moreInfoMap = \{\};/m, `var moreInfoMap = ${JSON.stringify(this.moreInfoMap)};`)
    },
    addGroup () {
      var group = {}
      this.formItems.map(item => {
        group[item.name] = ''
      })
      this.list.push(group)
    },
    deleteGroup (index) {
      this.list.splice(index, 1)
    }
  },
  created () {
    this.addGroup()
  },
  mounted () {
    editor = ace.edit('javascript-editor')
    editor.getSession().setMode('ace/mode/javascript')
    editor.setTheme('ace/theme/monokai')
    code = document.getElementById('code').innerText
  },
  components: {
    FormItem, CpInput, CpSelect
  }
}
</script>

<style>
#app{
  padding-top: 30px;
}
input,select,textarea {
  outline: none;
}
.moreField{
  margin-top: 30px;
  margin-bottom: 30px;
  border: 3px dashed #cccccc;
  padding: 10px 20px;
}
.group{
  border: 1px dashed #cccccc;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px 20px;
}
.button-field{
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 1000;
}
button{
  cursor: pointer;
}
a {
  cursor: pointer;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
  padding: 10px;
  width: 100%;
}
.cp-desc {
  font-weight: bold;
  font-style: italic;
  color: #555;
}
.cp-btn {
  font-size: 16px;
  font-style: normal;
  padding: 2px 10px;
  border-radius: 3px;
  color: #fff;
  background-color: #42b983;
  float: right;
}
.cp-btn.warn {
  background-color: #f60;
}
.cp-input_pluskey {
  width: 200px !important;
}
.javascript-editor {
  height: 500px;
}
</style>



