<template>
  <div id="app">
    <div class="button-field">
      <button id='add-btn' class='add-btn' type="button" name="button" @click='addGroup'>增加</button>
    </div>
    <div class="tip">
      支持图片、按钮等带有URL配置输入框的元素；生成的代码复制粘贴到原有代码的底部。
    </div>
    <div class="cp-form">
      <div class='group' v-for='(group, index) in list' v-bind:key='index'>
        <form-item :label="item.label" :name="item.name" :tip="item.tip" v-for="(item, n) in formItems" v-bind:key='n'>
          <CpInput v-if="item.type == 'input'" v-model="group[item.name]" :placeholder='item["placeholder"]'/>
          <a v-if='item.name=="scheme"' target="_blank" href="//blog.shellhong.com/wiki/#!appstore/deeplink.md">商店deeplink链接生成规则查看</a>
          <CpSelect v-if="item.type == 'select'" :options="item.options" v-model="group[item.name]" />
        </form-item>
        <div style="color:red;font-size:12px;margin-bottom:10px;margin-top:10px;text-align:center;">
          URL：javascript:void('{{group["packageName"]}}_{{group["index"]}}_scheme')
        </div>
        <button class='delete-btn' type="button" name="button" @click='deleteGroup(index)'>删除</button>
      </div>
    </div>
    <div class='javascript-editor' id="javascript-editor"></div>
    <pre id='code' style='display:none;'>
(function(){
  var schemeInfoMap = {};
  var appInfoHistory = {};
  var keys = Object.keys(schemeInfoMap);
  parent.postMessage({event: 'initSchemeInfoMap', schemeInfoMap: schemeInfoMap},'*');
  var addClickHandle = function(){
    keys.forEach(function(key){
      var btn = $('[data-url="javascript:void(\''+key+'\')"]').unbind().click(function(){
        parent.postMessage({event: 'openScheme', key: key},'*');
        return false;
      });
    });
  };
  window.addEventListener('orientationchange', function() {
    addClickHandle();
  });
  addClickHandle();
  function isElementInViewport(el) {
    var rect = el.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom &lt;= (window.innerHeight || document.documentElement.clientHeight) &&
      rect.right &lt;= (window.innerWidth || document.documentElement.clientWidth)
    );
  }
  var timeout = null;
  function exposurePosition() {
    keys.forEach(function(key){
      var schemeInfo = schemeInfoMap[key];
      if(schemeInfo.hasExposure){
        return;
      }
      var el = document.querySelector('[data-url="javascript:void(\''+key+'\')"]');
      if(!el){
        clearTimeout(timeout);
        timeout = setTimeout(exposurePosition, 3000);
        return;
      }
      if (isElementInViewport(el)) {
        parent.postMessage({
          event: 'mzDoTrack',
          trackInfo: {
            action: 'exposure_scheme_position',
            scheme: schemeInfo.scheme
          }
        },'*');
        schemeInfo.hasExposure = true;
      }
    });
  }
  window.addEventListener('scroll', exposurePosition, true);
  exposurePosition();
})();
    </pre>
  </div>
</template>

<script>
import FormItem from '@/components/common/FormItem'
import CpInput from '@/components/common/CpInput'
import CpSelect from '@/components/common/CpSelect'
import * as ace from 'brace'
import 'brace/mode/javascript'
import 'brace/theme/monokai'

var code = ''
var editor = null

export default {
  name: 'app',
  data () {
    return {
      formItems: [
        { type: 'input', label: 'deeplink链接', name: 'scheme', placeholder: '可为空，默认为打开应用' },
        { type: 'input', label: 'packageName', name: 'packageName' },
        { type: 'input', label: 'versionCode', name: 'versionCode' }
      ],
      list: [],
      index: 1
    }
  },
  computed: {
  },
  watch: {
    list: {
      handler: function (val) {
        editor && editor.setValue(this.getCode())
      },
      deep: true
    }
  },
  methods: {
    getCode () {
      var map = {}
      this.list.forEach(function (item) {
        item.versionCode = (item.versionCode !== '' ? parseInt(item.versionCode) : '')
        map[`${item.packageName}_${item.index}_scheme`] = item
      })
      return code.replace(/var schemeInfoMap = \{\};/m, `var schemeInfoMap = ${JSON.stringify(map)};`)
    },
    addGroup () {
      var group = {
        index: this.index++
      }
      this.formItems.map(item => {
        if (item.name === 'versionCode') {
          group[item.name] = 0
        } else {
          group[item.name] = ''
        }
      })
      this.list.push(group)
    },
    deleteGroup (index) {
      this.list.splice(index, 1)
    }
  },
  created () {
    this.addGroup()
  },
  mounted () {
    editor = ace.edit('javascript-editor')
    editor.getSession().setMode('ace/mode/javascript')
    editor.setTheme('ace/theme/monokai')
    code = document.getElementById('code').innerText
  },
  components: {
    FormItem, CpInput, CpSelect
  }
}
</script>

<style>
.tip{
  font-size: 12px;
  color: red;
  text-align: left;
  margin-top: 5px;
  margin-bottom: 5px;
}
a{
  color: #229de4;
  text-decoration:none;
  font-size: 12px;
}
input,select,textarea {
  outline: none;
}
.group{
  border: 1px dashed #cccccc;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px 20px;
}
.button-field{
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 1000;
}
button{
  cursor: pointer;
}
a {
  cursor: pointer;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
  padding: 10px;
  width: 100%;
}
.cp-desc {
  font-weight: bold;
  font-style: italic;
  color: #555;
}
.cp-btn {
  font-size: 16px;
  font-style: normal;
  padding: 2px 10px;
  border-radius: 3px;
  color: #fff;
  background-color: #42b983;
  float: right;
}
.cp-btn.warn {
  background-color: #f60;
}
.cp-input_pluskey {
  width: 200px !important;
}
.javascript-editor {
  height: 500px;
}
</style>



