import Vue from 'vue'
import Router from 'vue-router'
import MulReadMe from '@/components/mul/MulReadMe'
import MulUrlConfig from '@/components/mul/MulUrlConfig'
import MulCodeConfig from '@/components/mul/MulCodeConfig'
import MulSchemeConfig from '@/components/mul/MulSchemeConfig'
import FastCodeConfig from '@/components/fast/FastCodeConfig'
import FastReadMe from '@/components/fast/FastReadMe'
import FastUrlConfig from '@/components/fast/FastUrlConfig'
import CpCustomization from '@/components/CpCustomization'
import NavReadme from '@/components/nav/NavReadme'
import NavCodeConfig from '@/components/nav/NavCodeConfig'
import DevicesTest from '@/components/utils/DevicesTest'
import SchemeCheck from '@/components/utils/SchemeCheck'
import SingleReadMe from '@/components/single/SingleReadMe'
import SingleTest from '@/components/single/SingleTest'
import SingleUrlConfig from '@/components/single/SingleUrlConfig'
import SceneNotice from '@/components/SceneNotice'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/cpcustomization',
      name: 'CpCustomization',
      component: CpCustomization
    },
    {
      path: '/mulreadme',
      name: 'MulReadMe',
      component: MulReadMe
    },
    {
      path: '/mulurlconfig',
      name: 'MulUrlConfig',
      component: MulUrlConfig
    },
    {
      path: '/mulcodeconfig',
      name: 'MulCodeConfig',
      component: MulCodeConfig
    },
    {
      path: '/mulschemeconfig',
      name: 'MulSchemeConfig',
      component: MulSchemeConfig
    },
    {
      path: '/fastreadme',
      name: 'FastReadMe',
      component: FastReadMe
    },
    {
      path: '/fastcodeconfig',
      name: 'FastCodeConfig',
      component: FastCodeConfig
    },
    {
      path: '/fasturlconfig',
      name: 'FastUrlConfig',
      component: FastUrlConfig
    },
    {
      path: '/navreadme',
      name: 'NavReadme',
      component: NavReadme
    },
    {
      path: '/navcodeconfig',
      name: 'NavCodeConfig',
      component: NavCodeConfig
    },
    {
      path: '/devicestest',
      name: 'DevicesTest',
      component: DevicesTest
    },
    {
      path: '/schemecheck',
      name: 'SchemeCheck',
      component: SchemeCheck
    },
    {
      path: '/singlereadme',
      name: 'SingleReadMe',
      component: SingleReadMe
    },
    {
      path: '/singletest',
      name: 'SingleTest',
      component: SingleTest
    },
    {
      path: '/singleurlconfig',
      name: 'SingleUrlConfig',
      component: SingleUrlConfig
    },
    {
      path: '/scenenotice',
      name: 'SceneNotice',
      component: SceneNotice
    }
  ]
})



