<template lang="html">
  <div class="iframe-container" v-loading.lock="showLoading" element-loading-target='iframe'>
    <iframe :src="url"></iframe>
  </div>
</template>

<script>
export default {
  props: {
    url: String
  },
  data () {
    return {
      showLoading: true
    }
  },
  mounted () {
    document.querySelector('iframe').onload = () => {
      this.showLoading = false
    }
    document.querySelector('.right-field').style.overflow = 'hidden'
  },
  destroyed () {
    document.querySelector('.right-field').style.overflow = 'auto'
  }
}
</script>

<style lang="css">
  iframe, .iframe-container{
    border: 0;
    outline: 0;
    height: 100%;
    width: 100%;
  }
</style>



