<template>
  <div id="app">
    <div class="button-field">
      <button id='add-btn' class='add-btn' type="button" name="button" @click='addGroup'>增加</button>
    </div>

    <div class="cp-form">
      <div class='group' v-for='(group, index) in list' v-bind:key='index'>
        <form-item :label="item.label" :name="item.name" :tip="item.tip" v-for="(item, n) in formItems" v-bind:key='n'>
          <CpInput v-if="item.type == 'input'" v-model="group[item.name]" :placeholder='item["placeholder"]'/>
          <CpSelect v-if="item.type == 'select'" :options="item.options" v-model="group[item.name]" />
        </form-item>
        <div style="color:red;font-size:12px;margin-bottom:10px;margin-top:10px;text-align:center;">
          按钮URL：javascript:void('{{group["packageName"]}}')
        </div>
        <button class='delete-btn' type="button" name="button" @click='deleteGroup(index)'>删除</button>
      </div>
    </div>
    <div class='javascript-editor' id="javascript-editor"></div>
    <pre id='code' style='display:none;'>
(function(){
  var appInfoMap = {};
  var appInfoHistory = {};
  var keys = Object.keys(appInfoMap);
  parent.postMessage({event: 'initAppInfoMap', appInfoMap: appInfoMap},'*');
  window.addEventListener('message', function(e){
    var data = e.data;
    if(data.event == 'mzAppDownloadProgress') {
      appInfoHistory[data.packageName] = data.text;
      $('[data-url="javascript:void(\''+data.packageName+'\')"]').find('.ele').html(data.text);
    }
  }, false);
  var addClickHandle = function(isText){
    keys.forEach(function(packageName){
      var btn = $('[data-url="javascript:void(\''+packageName+'\')"]').unbind().click(function(){
        parent.postMessage({event: 'mzDownloadApp', packageName: packageName},'*');
        return false;
      });
      if(isText){
        btn.find('.ele').html(appInfoHistory[packageName]);
      }
    });
  };
  window.addEventListener('orientationchange', function() {
    addClickHandle(true);
  });
  addClickHandle(false);
  function isElementInViewport(el) {
    var rect = el.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom &lt;= (window.innerHeight || document.documentElement.clientHeight) &&
      rect.right &lt;= (window.innerWidth || document.documentElement.clientWidth)
    );
  }
  var timeout = null;
  function exposurePosition() {
    keys.forEach(function(packageName){
      var appInfo = appInfoMap[packageName];
      if(appInfo.hasExposure){
        return;
      }
      var el = document.querySelector('[data-url="javascript:void(\''+packageName+'\')"]');
      if(!el){
        clearTimeout(timeout);
        timeout = setTimeout(exposurePosition, 3000);
        return;
      }
      if (isElementInViewport(el)) {
        parent.postMessage({
          event: 'mzDoTrack',
          trackInfo: {
            action: 'exposure_position',
            apkname: appInfo.packageName,
            appid: appInfo.appId
          }
        },'*');
        appInfo.hasExposure = true;
      }
    });
  }
  window.addEventListener('scroll', exposurePosition, true);
  exposurePosition();
})();
    </pre>
  </div>
</template>

<script>
import FormItem from '@/components/common/FormItem'
import CpInput from '@/components/common/CpInput'
import CpSelect from '@/components/common/CpSelect'
import * as ace from 'brace'
import 'brace/mode/javascript'
import 'brace/theme/monokai'

var code = ''
var editor = null

export default {
  name: 'app',
  data () {
    return {
      formItems: [
        { type: 'input', label: 'deeplink链接', name: 'scheme', placeholder: '可为空，默认为打开应用' },
        { type: 'input', label: 'packageName', name: 'packageName' },
        { type: 'input', label: 'versionCode', name: 'versionCode' },
        { type: 'input', label: 'appId', name: 'appId' },
        { type: 'input', label: 'installText', name: 'installText', placeholder: '可为空，默认为：安装' },
        { type: 'input', label: 'updateText', name: 'updateText', placeholder: '可为空，默认为：更新' },
        { type: 'input', label: 'installCompleteText', name: 'installCompleteText', placeholder: '可为空，默认为：打开' },
        { type: 'input', label: 'failureText', name: 'failureText', placeholder: '可为空，默认为：重试' }
      ],
      list: []
    }
  },
  computed: {
  },
  watch: {
    list: {
      handler: function (val) {
        editor && editor.setValue(this.getCode())
      },
      deep: true
    }
  },
  methods: {
    getCode () {
      var map = {}
      this.list.forEach(function (item) {
        item.appId = (item.appId !== '' ? parseInt(item.appId) : '')
        item.versionCode = (item.versionCode !== '' ? parseInt(item.versionCode) : '')
        map[item.packageName] = item
      })
      return code.replace(/var appInfoMap = \{\};/m, `var appInfoMap = ${JSON.stringify(map)};`)
    },
    addGroup () {
      var group = {}
      this.formItems.map(item => {
        if (item.name === 'versionCode') {
          group[item.name] = 0
        } else {
          group[item.name] = ''
        }
      })
      this.list.push(group)
    },
    deleteGroup (index) {
      this.list.splice(index, 1)
    }
  },
  created () {
    this.addGroup()
  },
  mounted () {
    editor = ace.edit('javascript-editor')
    editor.getSession().setMode('ace/mode/javascript')
    editor.setTheme('ace/theme/monokai')
    code = document.getElementById('code').innerText
  },
  components: {
    FormItem, CpInput, CpSelect
  }
}
</script>

<style>
input,select,textarea {
  outline: none;
}
.group{
  border: 1px dashed #cccccc;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px 20px;
}
.button-field{
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 1000;
}
button{
  cursor: pointer;
}
a {
  cursor: pointer;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
  padding: 10px;
  width: 100%;
}
.cp-desc {
  font-weight: bold;
  font-style: italic;
  color: #555;
}
.cp-btn {
  font-size: 16px;
  font-style: normal;
  padding: 2px 10px;
  border-radius: 3px;
  color: #fff;
  background-color: #42b983;
  float: right;
}
.cp-btn.warn {
  background-color: #f60;
}
.cp-input_pluskey {
  width: 200px !important;
}
.javascript-editor {
  height: 500px;
}
</style>



